import React from 'react';
import { Box, Typography, Tab, Tabs } from '@mui/material';
import TabPanel from '../../tabs/components/TabPanel3';
import transactionstabs from '../../tabs/options/transactionstabs'; 

export default function TransactionsPage() {

    const options = transactionstabs;
    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ m: 1 }}> 
            <Box sx={{ maxWidth: '100%', mt: 0, bgcolor: 'background.paper' }}>
                <Tabs
                    value={value}
                    sx={{ p: 0, pt: 0, borderBottom: '1px solid #ccc', mb: 1 }}
                    TabIndicatorProps={{
                        style: {
                            display: "none"
                        }
                    }}
                >
                    {
                        options.map((data, index) => {
                            return <Tab
                                key={`tabkey-${index}`}
                                sx={{
                                    minWidth: 200,
                                    backgroundColor: (index === value) ? "#00bfa5" : "#FFFFF",
                                    borderTopLeftRadius: 5,
                                    borderTopRightRadius: 5,
                                    border: '1px solid #ccc'
                                }}
                                onClick={() => handleChange(index)}
                                label={
                                    <Typography variant="caption1" color={(index === value) ? "#FFF" : "#000"}>
                                        <Box display="flex">
                                            {data.icon}
                                            {data.name}
                                        </Box>
                                    </Typography>
                                }
                            />

                        })
                    }
                </Tabs>
                {
                    options.map((data, index) => {
                        return <TabPanel height={550} value={value} index={index} key={`tabpanelkey-${index}`}>
                            {data.content({ handleChange: () => handleChange(0) })}
                        </TabPanel>
                    })
                }
            </Box>
        </Box>
    )
}
